import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-signin',
  templateUrl: 'signin.html'
})
export class Signin {
  selectedItem: any;
  icons: string[];
  items: Array<{ title: string, note: string, icon: string }>;

  constructor(public navCtrl: NavController, public navParams: NavParams) {

  }

  validatePhone() {
    var number = document.getElementById('phone').innerText,
      regex1 = /^([0-9]+){9}$/,
      regex2 = /\s/;

    if (number.match(''))
      alert('Es necesario tu numero de celular para poder ingresar/registrarte');
    else if (regex1.test(number))
      alert('Número incompleto');
    else if (regex2.test(number))
      alert('Número incorrecto, espacio');
    else
      this.signup();
  }

  signup() {
    this.navCtrl.push('Signup');
  }
}


